<template>
  <div class="docs_container">
    <pr-row>
      <pr-col span="4"
        v-show="$root.isMenuVisible">
        <!-- <div>LOGO</div> -->
        <div class="docs_side_bar"
          :style="{width: $root.isMobile ? '200px' : 'inherit'}">
          <pr-scroller scroll-y="calc(100vh - 51px)"
            :bar-style="barStyle"
            :thumb-style="thumbStyle">
            <div class="docs_side_bar_wrapper">
              <ul class="docs_list">
                <li v-for="doc in docs"
                  :key="doc.name"
                  @click="$root.isMenuVisible = !$root.isMobile">
                  <h3>{{ doc.title }}</h3>
                  <router-link tag="span"
                    class="pr-ellipsis"
                    v-for="child in doc.children"
                    :to="`/doc/${child.path}`"
                    :key="child.name">
                    {{ child.title || child.name }}
                  </router-link>
                </li>
              </ul>
            </div>
          </pr-scroller>
        </div>
      </pr-col>
      <pr-col span="20"
        offset="4">
        <div class="docs_content">
          <router-view />
          <div v-if="nextPost.path"
            class="docs_content-next">
            下一篇：
            <router-link :to="nextPost.path">
              {{nextPost.title}} - {{nextPost.name}}
            </router-link>
          </div>
        </div>
      </pr-col>
    </pr-row>
  </div>
</template>

<script>
import '@/assets/style/docs.less'
import docs from '../gnozValidatorDoc'
export default {
  name: 'docs',
  data() {
    return {
      docs,
      barStyle: {
        x: {
          background: 'transparent'
        },
        y: {
          background: 'transparent'
        }
      },
      thumbStyle: {
        background: '#c1c1c1'
      }
    }
  },
  computed: {
    nextPost() {
      let path = this.$route.path.split('/')[2]
      let isGet = false
      let currentSortIndex = null
      let currentSortChildIndex = null
      for (let i = 0; i < this.docs.length; i++) {
        let sort = this.docs[i]
        for (let j = 0; j < sort.children.length; j++) {
          if (path === sort.children[j].path) {
            currentSortIndex = i
            currentSortChildIndex = j
            isGet = true
            break
          }
        }
        if (isGet) break
      }

      let nextPost = this.docs[currentSortIndex]['children'][
        currentSortChildIndex + 1
      ]

      let nextSortIndex = nextPost ? currentSortIndex : currentSortIndex + 1
      let nextSortChildIndex = nextPost ? currentSortChildIndex + 1 : 0

      return this.docs[nextSortIndex]
        ? {
            ...this.docs[nextSortIndex]['children'][nextSortChildIndex],
            title: this.docs[nextSortIndex].title
          }
        : {}
    }
  }
}
</script>



